@import "ScssHelpers/helpers-all.scss";

.onboarding_form {
	color: $black
}

.onboarding_form__info {
	max-width: 112rem;
	width: 100%;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 5.8rem;
		}
		@include bp($point_3) {
			margin-bottom: 3rem;
		}
	}
}

.onboarding_form__body {
	max-width: 60rem;
	margin: 0 auto;
	width: 100%;
}

.onboarding_form__title {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4.2rem;
		}
		@include bp($point_3) {
			margin-bottom: 1.6rem;
		}
	}

	@include h3;

	@include bp($point_2, min) {
		text-align: center;
	}
}

.onboarding_form__form {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2rem;
		}
		@include bp($point_3) {
			margin-bottom: 1.6rem;
		}
	}
}

.onboarding_form__items {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4.2rem;
		}
		@include bp($point_3) {
			margin-bottom: 3.2rem;
		}
	}
}

.onboarding_form__item {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4.2rem;
		}
		@include bp($point_3) {
			margin-bottom: 2.4rem;
		}
	}
}

.onboarding_form__button {
	width: 100%;
}

.onboarding_form__alt {
	text-align: center;
	line-height: 1.6;

	a {
		color: $gold_2;
		font-weight: 500;
		text-decoration: underline;

		&:hover,
		&:focus{
			text-decoration: none;
		}
	}

	@include bp($point_2, min) {
		font-size: 1.5rem;
	}
	@include bp($point_3) {
		font-size: 1.4rem;
	}
}