@import "info-item";

.cooking_info_list {
	// padding: 9.6rem var(--gl-indent);
}

.cooking_info_list__bg {
	z-index: -1;
	position: absolute;
	top: 0;
	left: 0;

	display: block;
	width: 100%;
	height: 100%;
}

.cooking_info_list__bg_img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.cooking_info_list__wrap {
	background-color: $white;

	@include bp($point_2, min) {
		padding: 4.6rem 7rem 6.6rem 7.2rem;
	}

	@include bp($point_3) {
		padding: 3rem 2.4rem;
	}
}

.cooking_info_list__array {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, center, flex-start, flex-start);
		margin: 0 -3.8rem -4.8rem 0;
	}

	@include bp($point_3, $point_4) {
		@include flex_block(row, wrap, center, flex-start, flex-start);
		margin: 0 -3.8rem -4.8rem 0;
	}
}

.cooking_info_list__item {
	@include bp($point_2, min) {
		width: 25%;
		padding: 0 3.8rem 4.8rem 0;
	}

	@include bp($point_3, $point_4) {
		width: 50%;
		padding: 0 3.8rem 4.8rem 0;
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}
}
