@import "ScssHelpers/helpers-all.scss";

.info_item {
	.cooking_info_list &,
	.cooking_tips_list &,
	.steak_club_info & {
		@include bp($point_4 - 1) {
			display: flex;
			align-items: flex-start;
			justify-content: flex-start;
		}
	}
}

.info_item__img {
	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 2.2rem;
		}
	}

	.cooking_info_list & {
		@include bp($point_2, min) {
			width: 14rem;
			height: 14rem;
		}
	}

	.steak_club_info &,
	.cooking_tips_list & {
		@include bp($point_2, min) {
			width: 13.2rem;
			height: 13.2rem;
		}

		@include bp($point_4 - 1) {
			&:not(:last-child) {
				margin-right: 2.2rem;
			}
		}
	}

	@include bp($point_3, $point_4) {
		width: 9rem;
		height: 9rem;
	}

	@include bp($point_4 - 1) {
		flex-shrink: 0;
		width: 6.2rem;
		height: 6.2rem;
	}
}

.info_item__img_in {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.info_item__content {
	padding-left: 1.4rem;

	.cooking_tips_list & {
		padding-left: 0.8rem;
	}

	.steak_club_info & {
		@include bp($point_4, min) {
			padding-left: 0.6rem;
		}

		@include bp($point_4 - 1) {
			padding-left: 0;
			padding-top: 0.4rem;
		}
	}
}

.info_item__title {
	@include h4;
	letter-spacing: -0.01em;
	line-height: 1.14;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 1rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 0.6rem;
		}
	}

	&:not(.steak_club_info &) {
		@include bp($point_4, min) {
			min-height: 2.7em;
		}
	}

	.cooking_tips_list &,
	.steak_club_info & {
		&:not(:last-child) {
			margin-bottom: 0.6rem;
		}
	}

	@include bp($point_2, min) {
		margin-right: 2.2rem;

		&:not(.steak_club_info &) {
			min-height: 2em;
		}
	}
}

.info_item__description {
	font-weight: 400;

	@include bp($point_4, min) {
		font-size: 1.5rem;
		letter-spacing: normal;
		line-height: 1.47;
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
		line-height: 1.42;
		letter-spacing: -0.014em;
	}
}
