@import "ScssHelpers/helpers-all.scss";

.step_form_head {
	position: relative;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.2rem;
		}
		@include bp($point_3) {
			margin-bottom: 2.2rem;
		}
	}

	&.step_form_head--centered_mod {
		text-align: center;
	}

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, flex-end);
	}
}

.step_form_head__wrap {
	flex: auto;

	&:not(.step_form_head--centered_mod &) {
		// padding-right: 4rem;
	}

	&:not(:last-child) {
		margin-bottom: 1.6rem;
	}
}

.step_form_head__title {
	@include h3;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1rem;
		}

		@include bp($point_3) {
			// margin-bottom: 0.8rem;
			margin-bottom: 0.6rem;
		}
	}

	.step_form_head--centered_mod & {
		margin: 0 auto 1rem;
	}

	@include bp($point_2, min) {
		max-width: 95rem;
	}
	@include bp($point_3) {
		letter-spacing: -0.03em;
	}
}

.step_form_head__text {
	@include text;
	max-width: 95rem;

	@include bp($point_2, min) {
		line-height: 1.5;
	}
	@include bp($point_3) {
		line-height: 1.45;
		letter-spacing: -0.015em;
	}

	.step_form_head--centered_mod & {
		margin: 0 auto;
	}
}

.step_form_head__button .btn_1{
	@include bp($point_6 - 1) {
		width: 100%;
	}
}
